@import "./Styles.scss";

.listWrapper {
  height: 100%;
  padding: 0;
  margin: 0;
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  overflow: auto;

  &--listItem {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 10px;
    margin: 10px;
    background-color: $listBackground;
    border-radius: 10px;
    min-width: 280px;
    max-width: 280px;
    height: 280px;

    &--contentWrapper {
      margin: 20px;
      &--general,
      &--details {
        display: flex;
        flex-direction: column;
        justify-content: center;
      }

      &--general {
        align-items: center;
      }

      &--details {
        padding: 0;
        margin: 0;
        text-align: center;
        li {
          list-style: none;
          margin-bottom: 10px;
        }
      }
    }

    &--general {
      &--fullName {
        font-size: 24px;
        text-align: center;
        color: black;
      }
    }
  }
}
